import React from "react";
import {ElevatorProps} from '../elevator-page/elevator-types';

class Elevator extends React.Component<ElevatorProps, {}> {
    render () {
        const {floor, direction} = this.props;
        const directionClasses = `elevator--directions elevator--directions--${direction}`;

        return (<div className="elevator">
            <div className="elevator--floor-display">{floor}</div>
            <div className={directionClasses}>
                <div className="elevator--direction--up">{`\u00b7`}</div>
                <div className="elevator--direction--up">{`\u00b8`}</div>
            </div>
        </div>);
    }
}

export default Elevator;